import React, { useState, useEffect } from "react"
import $ from "jquery"
import { scrollHome } from "./Layout/jsUtils"

const TopButton = () => {
  const [show, setShow] = useState(false)

  useEffect(() => {
    $(window).on('load', () => isVisibleCheck())
    return () => $(window).off()
  }, [])

  const isVisibleCheck = () => {
    const screenTop = $(window).scrollTop()
    const headerHeight = $(".header").outerHeight()
    if (screenTop && headerHeight) {
      screenTop > headerHeight ? setShow(true) : setShow(false)
    }
  }

  return (
    <button
      onClick={() => scrollHome()}
      className={`btn btn-secondary top-button ${show && 'show'}`}
    >
      Top
    </button >
  )
}

export default TopButton
